import { NgModule} from '@angular/core';
import { SharedModule} from '../../shared/modules/shared.module';
import { SideNavbarComponent } from './../components/+sideNavbar/index';
import { AccountComponent } from './account/index';
import { MembershipComponent } from './membership/index';
import { TeamSettingComponent } from './teamSetting/index';
import { ApiKeyComponent } from './apiKey/index';
import { NotificationComponent } from './notification/index';
import { SettingsComponent } from './settings.component';
import { SettingsCommunicationService } from '../../shared/services/index';
import { Routes, RouterModule} from '@angular/router';
import { ControlsModule} from '../templates/controls/controls.module';
import { ToolbarModule } from './../components/toolbar/toolbar.module';
import { PaymentComponent } from './membership/payment/payment.component';
import { SubscriptionComponent } from './membership/subscription/subscription.component';
import { InvoiceComponent } from './membership/invoice/invoice.component';
import { UserCompaniesComponent } from './account/userCompanies/usercompanies.component';
import { ChangePasswordComponent } from './account/changePassword/changepassword.component';
import { BasicDetailsComponent } from './account/basicDetails/basicdetails.component';
import { CompanyService } from '../../shared/services/company.service';
import { UserService } from '../../shared/services/user.service';
import { LoggedInService } from '../../shared/services/logged-in.service';
import { CookieService } from '../../shared/services/cookie.service';
import { Script } from '../../shared/services/script.service';
import { MembershipService } from '../../shared/services/membership.service';
import { PlanService } from '../../shared/services/plan.service';
import { IntegrationService } from '../../shared/services/integration.service';
import { ReferralService } from '../../shared/services/referral.service';
import { IntegrationsComponent } from './integrations/integrations.component';
import { IntegrationsGuard } from './../../shared/authentication/integrations.guard';
const SETTINGS_ROUTES: Routes = [
  {
    path: '',
    component: SettingsComponent,
    children: [
      {
        path: '',
        component: TeamSettingComponent
      },
      {
        path: 'membership',
        component: MembershipComponent
      },
      {
        path: 'api-key',
        component: ApiKeyComponent
      },
      {
        path: 'my-account',
        component: AccountComponent
      },
      {
        path: 'integrations',
        component: IntegrationsComponent
      }
      ]
  }
];

@NgModule({
  imports: [SharedModule, RouterModule.forChild(SETTINGS_ROUTES), ControlsModule, ToolbarModule],
  declarations: [
    SettingsComponent,
    SideNavbarComponent,
    AccountComponent,
    MembershipComponent,
    TeamSettingComponent,
    NotificationComponent,
    ApiKeyComponent,
    PaymentComponent,
    SubscriptionComponent,
    InvoiceComponent,
    UserCompaniesComponent,
    ChangePasswordComponent,
    BasicDetailsComponent,
    IntegrationsComponent
  ],
  providers: [
    SettingsCommunicationService,
    CompanyService,
    UserService,
    LoggedInService,
    CookieService,
    Script,
    MembershipService,
    PlanService,
    IntegrationService,
    IntegrationsGuard
  ],
  exports: [SettingsComponent]
})
export class SettingsModule {}



