import { Component, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';
import { SettingsCommunicationService } from '../../../shared/services/index';

declare var jQuery:any;
@Component({
  selector: 'og-account',
  templateUrl: './account.component.html',
  encapsulation: ViewEncapsulation.None,
	styleUrls: ['./account.component.css','./custom-material.css','./../settings.component.css']
})

export class AccountComponent{

  myCompanies:any;
  constructor(public router: Router,private settingsCommunicationService : SettingsCommunicationService) {
    this.myCompanies = this.settingsCommunicationService.companyList;
    jQuery.material.init();
  }
}



