import {Component, OnInit, Input, ViewEncapsulation,AfterViewInit} from '@angular/core';
import { SettingsCommunicationService } from '../../shared/services/index';
import {Script, FeatureAuthService} from '../../shared/services/index';
import {MarketingService} from '../../shared/services/marketing.service';
import {Title} from '@angular/platform-browser';
import {FeatureAccess} from '../../shared/interfaces/features.interface';
declare var jQuery:any;
@Component({
  selector: 'og-settings',
  templateUrl: './settings.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./settings.component.css']
})

export class SettingsComponent implements OnInit,AfterViewInit{
  sidenav:any;
  sideNavbar:any;
  Message:any;
  selectedCompany:any;
  CompaniesList:any;
  selUserAdmin:any;
  isRequested:any;
  constructor(private settingsCommunicationService:SettingsCommunicationService,
              private _script:Script,
              private _marketingService:MarketingService,
              private titleService: Title,
              private _featureAuthService : FeatureAuthService) {
    this.titleService.setTitle("Outgrow Home");
    this.CompaniesList = this.settingsCommunicationService.companyList;
    this._script.load('marketing')
      .then((data) => {
        //console.log('Scripts Loaded', data);
        if(data.length && data[0].status == 'Loaded')
          _marketingService.initMarketingStuff();
      })
      .catch((error) => {
        //any error
      });
  }
  ngOnInit():any {
    this._featureAuthService.getAllFeatureAccess().subscribe((result) => {
      this._featureAuthService.features = new FeatureAccess(result);
    });
    this.Message = 'message';
    this.sideNavbar = 'settings';
    this.sidenav = 1;
  }

ngAfterViewInit() {

    /*hide help icon on dashboard for mobile screen */
    setTimeout(() => jQuery('.builder-help-icon').addClass('hide'), 2000);
  }

}




