  getAllConfiguration() {
    this._integrationService.getAllConfiguration()
      .subscribe((result) => {
        this.configuration.salesforce = result.salesforce ? result.salesforce : false;
        this.configuration.marketo = result.marketo ? result.marketo : false;
        this.configuration.mailchimp = result.mailchimp ? result.mailchimp : false;
        this.configuration.getresponse = result.getresponse ? result.getresponse : false;
        this.configuration.activeCampaign = result.active_campaign ? result.active_campaign_config : false;
        this.configuration.aweber = result.aweber ? result.aweber : false;
      }, (error) => {
        //console.log(error);
      });
  }

   getConfiguration() {
    this._integrationService.getConfiguration()
      .subscribe((result) => {
         if (result.marketo_config) {
            this.integrations.client_id = result.marketo_config.client_id;
            this.integrations.client_secret = result.marketo_config.client_secret;
            this.integrations.ideantity = result.marketo_config.instance_url;
         }
        if(result.mailchimp_config){
           this.integrations.api_key = result.mailchimp_config.api_key ;
        }
        if(result.getresponse_config){
          this.integrations.apiKey = result.getresponse_config.api_key ;
        }
        if(result.active_campaign_config){
          this.activeCampaignIntegrations.api_key = result.active_campaign_config.api_key ;
          this.activeCampaignIntegrations.account = result.active_campaign_config.instance_url;
        }
      }, (error) => {
        //console.log(error);
      });
   }

  getsyncLead() {
      this.isSyncEnableMarketo = false;
      this.isSyncEnableSF = false;
      this.isSyncEnableActiveCampaign = false;
      this._integrationService.getCompanySyncLeads()
      .subscribe((result) => {
      console.log("@@@@@@@@@@@@@@@@@@@",result);
       this.pendingLeadsSF = result.salesforce + ' item';
       this.pendingLeadsMarketo = result.marketo + ' item';
       this.pendingLeadsMailchimp = result.mailchimp + ' item';
       this.pendingLeadsGetresponse = result.getresponse + 'item';
       this.pendingLeadsActiveCampaign = result.active_campaign + ' item';
       this.pendingAweber = result.aweber + ' item';
       if (result.marketo >= 1) {
         this.isSyncEnableMarketo = true;
       }
       if (result.salesforce >= 1) {
         this.isSyncEnableSF = true;
       }
       if (result.mailchimp >= 1) {
         this.isSyncEnableMailchimp = true;
       }
       if (result.getresponse >= 1) {
         this.isSyncEnableGetresponse = true ;
       }
       if (result.active_campaign >= 1) {
         this.isSyncEnableActiveCampaign = true;
       }
       if (this.pendingAweber >= '1') {
                this.isSyncEnableAweber = true;
        }

      }, (error) => {
        //console.log(error);
      });
  }

  syncLead(type: string) {
     this.integrationType = type;
     let self = this;
     if (type === 'marketo') {
        jQuery('.sync-leads-marketo').html('Please Wait..');
     }
     if(type==='salesforce') {
        jQuery('.sync-leads-sf').html('Please Wait..');
     }if(type==='mailchimp') {
        jQuery('.sync-leads-mailchimp').html('Please Wait..');
     }
     if(type==='getresponse') {
        jQuery('.sync-leads-getresponse').html('Please Wait..');
     }
     if(type==='active_campaign') {
        jQuery('.sync-leads-activeCampaign').html('Please Wait..');
     }
    this._integrationService.syncLeads(type)
      .subscribe((result) => {
        if (result.success) {
          //console.log(result,'*********************');
          if (type === 'marketo') {
             self.isSyncEnableMarketo = false;
             jQuery('.sync-leads-marketo').html('Sync');
          }
          if(type==='salesforce') {
            self.isSyncEnableSF = false ;
            jQuery('.sync-leads-sf').html('Sync');
          }if(type==='mailchimp') {
            self.isSyncEnableSF = false ;
            jQuery('.sync-leads-mailchimp').html('Sync');
          }
          if(type==='getresponse') {
            self.isSyncEnableGetresponse = false ;
            jQuery('.sync-leads-getresponse').html('Sync');
          }
           if(type==='active_campaign') {
             this.isSyncEnableActiveCampaign = false ;
          jQuery('.sync-leads-activeCampaign').html('Sync');
         }
          self.getsyncLead();
          window.toastNotification('Leads will be synced to connected ' + type + ' for this calculator');
        }else {
            if (type === 'marketo') {
              self.isSyncEnableMarketo = true;
              jQuery('.sync-leads-marketo').html('Sync (' + self.pendingLeadsMarketo + ')');
            }
            if (type === 'salesforce') {
              self.isSyncEnableSF = true;
              jQuery('.sync-leads-sf').html('Sync (' + self.pendingLeadsSF + ')');
            }if (type === 'mailchimp') {
              self.isSyncEnableSF = true;
              jQuery('.sync-leads-mailchimp').html('Sync (' + self.pendingLeadsMailchimp + ')');
            }
            if (type === 'getresponse') {
              self.isSyncEnableGetresponse = true;
              jQuery('.sync-leads-getresponse').html('Sync (' + self.pendingLeadsGetresponse + ')');
            }
             if (type === 'active_campaign') {
              this.isSyncEnableActiveCampaign = true;
              jQuery('.sync-leads-activeCampaign').html('Sync (' + this.pendingLeadsActiveCampaign + ')');
            }
            jQuery('#syncError-modal').modal('show');
        }
      }, (error) => {
          if (type === 'marketo') {
            self.isSyncEnableMarketo = true;
            jQuery('.sync-leads-marketo').html('Sync (' + self.pendingLeadsMarketo + ')');
          }
          if(type==='salesforce') {
            self.isSyncEnableSF = true;
            jQuery('.sync-leads-sf').html('Sync (' + self.pendingLeadsSF + ')');
          }if(type==='mailchimp') {
            self.isSyncEnableSF = true;
            jQuery('.sync-leads-mailchimp').html('Sync (' + self.pendingLeadsMailchimp + ')');
          }
          if(type==='getresponse') {
            self.isSyncEnableGetresponse = true;
            jQuery('.sync-leads-getresponse').html('Sync (' + self.pendingLeadsGetresponse + ')');
          }
           if(type==='active_campaign') {
            this.isSyncEnableActiveCampaign = true;
            jQuery('.sync-leads-activeCampaign').html('Sync (' + this.pendingLeadsActiveCampaign + ')');
          }
          jQuery('#syncError-modal').modal('show');
      });
   }

